
body{
  background: #F2F4F7;
}
.qy-wrap{
  padding-bottom: 1.3rem;
}
.ellipsis {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.ellipsis-list-3{
  display: -webkit-box;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
.qy-detail-wrap{
  width:90%;
  margin:0 auto;
  padding:.2rem 0;
}
.qy-detail-group{
  background: #fff;
  margin-bottom:.2rem;
}
.qy-detail-btn-all{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:distribute;
      justify-content:space-around;
  background: #fff;
  width:100%;
  padding:.2rem;
  position: fixed;
  bottom: 0;
  left:0;
}
.qy-detail-btn-all a{
    border: 1px solid #b9bdc0;
    font-size: .4rem;
    padding: .2rem 0.6rem;
    border-radius: .5rem;
}
.qy-detail-btn-all .qy-go-read{
  background:#f46094;
  border-color:#f46094;
  color:#fff;
}



